/* --- 1. Import Fonts --- */
@import url('https://fonts.googleapis.com/css2?family=Sora:wght@200;300;400;600;700;800&display=swap');
$font-stack: Sora;


/* --- 2. Colors --- */
$primary-color: #40A3FF;
$secondary-color: #333;

$dark-blue: #071F35;
$light-blue: #F3F9FF; 
$light-grey: #F0EDED;
$greenish-blue: #5F84A6;
$grey: #E5E5E5;
$white: #fff;
$black: #000;
$dark: #272727;

/* --- 3. Global --- */
%paragraph{
  color: $white;
  font-weight: 300;
  font-size: 18px;
}
%heading{
  color: $white;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: -0.02em;
}
%arrow{
  content: " ";
  width: 50px;
  height: 20px;
  margin-left: 40px;
  top: 4px;
  position: relative;
  display: inline-block;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  cursor: pointer;
}
