%linkTransition{
  position: relative;
  visibility: visible;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
  background-image: linear-gradient(180deg, transparent 92%, rgba(255, 255, 255, 0.87) 0);
  background-size: 0 100%;
  background-repeat: no-repeat;
  text-decoration: none;
  -webkit-transition: background-size .4s ease;
  -moz-transition: background-size .4s ease;
  -ms-transition: background-size .4s ease;
  -o-transition: background-size .4s ease;
  transition: background-size .4s ease;
}
%linkTransitionActive{
  background-size: 100% 100%;
  cursor: pointer;
}

.container{
  max-width: 1280px;
}
body{
  font-family: $font-stack;
  overflow-x: hidden;
}
p{
  @extend %paragraph;
}
h1{
  @extend %heading;
  font-size: 96px;
  line-height: 120%;
  font-weight: 800;
}
h2{
  @extend %heading;
  font-size: 72px;
  line-height: 91px;
}
h3{
  @extend %heading;
  font-size: 18px;
  line-height: 23px;
}
h4{
  @extend %heading;
  font-size: 24px;
  line-height: 30px;
}
h5{
  color: $greenish-blue;
  font-size: 14px;
  line-height: 18px;
}
h6{
  @extend %heading;
  font-size: 14px;
  line-height: 18px;
}
a{
  color: $white;
  font-weight: 600;
  font-size: 18px;
  position: relative;
  display: inline-block;
}
a:hover{
  text-decoration: none;
}
a.e-hover, h3 span, .e-link span {
  @extend %linkTransition;
}
a.e-hover:hover, h3:hover span, .e-link:hover span {
  @extend %linkTransitionActive;
}



.e-link-arrow::after, .e-white-wrrow{
  @extend %arrow;
  background-image: url("../images/icons/right-arrow-white.svg");
}
.e-link-arrow-black::after{
  @extend %arrow;
  background-image: url("../images/icons/right-arrow-black.svg");
}
.e-link-arrow-blue::after{
  @extend %arrow;
  background-image: url("../images/icons/right-arrow-blue.svg");
}

/* -- Hero Banner -- */
.e-hero-banner{
  background: $black;
  min-height: 100vh;
}
.e-hero-banner{
  .slick-list{
   overflow: visible;
  }
  .e-heading-wrap{
    height: 100vh;
    padding-top: 180px;
    left: calc((1140px - 100%)/2);
    z-index: 1;
    /*position: absolute;
    z-index: 99;
    top: 200px;
    left: calc((1140px - 100%)/2);*/
  }
  .e-branding{
    position: absolute;
    top: 200px;
    right: 0;
    opacity: .8;
    z-index: 0;
  }
  .e-expertise-wrap{
    bottom: 0;
    right: 0;
    position: absolute;
    width: 100%;
    z-index: 3;
  }
  .e-expertise{
    background: rgba(0, 0, 0, 0.48);
    backdrop-filter: blur(24px);
    padding: 40px;
  }
  .e-overlay{
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    background: rgb(0 0 0 / 34%);
  }
  .container{
    margin-right: 0;
    position: relative;
  }


  .slick-next {
    background: url('../images/icons/hero-slider-arrow-right.svg') center center no-repeat!important;
    &::before {
      display: none;
    }
  }
  .slick-prev {
    background: url('../images/icons/hero-slider-arrow-left.svg') center center no-repeat!important;
    left: initial;
    right: 60px;
    display: none;
    &::before {
      display: none;
    }
  }
  .slick-prev, .slick-next{
    width: 50px;
    height: 50px;
    left: 250px;
    background-size: cover;
    position: absolute;
    top: initial;
    bottom: 50px;
    z-index: 3;
  }
  .slick-prev{
    right: initial;
    left: 200px;
  }
  .e-slide-indicator{
    @extend %paragraph;
    font-weight: 600;
    letter-spacing: 1px;
    font-size: 12px;
    position: relative;
    margin-left: -60px;
    display: inline-block;
  }
  .e-slide-indicator::after{
    content: " ";
    width: 200px;
    height: 1px;
    background: $white;
    display: inline-block;
    margin-left: 15px;
    position: relative;
    top: -3px;
  }
}
.e-banner-video, .e-banner-image{
  position: absolute;
  width: 100%;
  right: 0;
  top: 0px;
}








