/* -- BG image slider -- */
.e-bg-img-slider-wrap{
  background-size: cover;
  background-repeat: no-repeat;

  transition: all 0.5s ease-out;

  .slick-next {
    background: url('../images/icons/right-arrow-white.svg') center center no-repeat!important;
    &::before {
      display: none;
    }
  }
  .slick-prev {
    background: url('../images/icons/left-arrow-white.svg') center center no-repeat!important;
    left: initial;
    right: 60px;
    display: none;
    &::before {
      display: none;
    }
  }
  .slick-prev, .slick-next{
    top: -100px;
    width: 50px;
    height: 50px;
    background-size: cover;
  }
}
.e-slider-content{
  height: 520px;
  border-right: 1px solid rgb(255 255 255 / 50%);
  padding: 30px 30px 100px 30px;
  cursor: pointer;
}
.e-slider-content{
  h3{
    margin-bottom: 30px;
  }
}



/* -- Testimonials -- */
.e-testimonials{
  background-color: $light-grey;
  padding: 100px 0;
}
.e-testimonials{
  h2{
    color: $dark-blue;
  }
  .e-overlay{
    background: linear-gradient(180deg, rgba(240, 237, 237, 0) 0.63%, #F0EDED 27.2%);
    height: 640px;
    width: 100%;
    position: absolute;
    bottom: 0;
  }
}
.e-client-card{
  background: #E8E4E4;
  padding: 20px 0;
  background-blend-mode: multiply;
  margin-bottom: 30px;
}
.e-testimonial-card{
  background: url("../images/bg/review.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  padding: 100px;
  position: relative;
  z-index: 1;
}
.e-testimonial-card{
  .e-testimonial-content{
    font-size: 18px;
  }
  h6{
    font-size: 18px;
    font-weight: 600;
  }
  .e-testimonial-designation{
    font-weight: 200;
    opacity: .8;
  }
  .slick-prev, .slick-next {
    display:none !important;
  }
  .slick-dots li button:before{
    opacity: 1;
    color: #86C5FF;
    font-size: 8px;
  }
  .slick-dots li.slick-active button:before{
    opacity: 1;
    color: $white;
  }
}




/* -- Latest Stories -- */
.e-latest-stories{
  background-color: $black;
  padding: 100px 0;
}
.e-latest-stories-card{
  position: relative;
  cursor: pointer;
  cursor: url('../images/icons/custom-pointer.svg'), auto;
  &:hover h4 span{
    @extend %linkTransitionActive;
  }
}
.e-latest-stories-card{
  h4{
    padding: 0 25px;
    position: absolute;
    bottom: -30px;
  }
  h4 span{
    @extend %linkTransition;
  }
  .e-overlay{
    background: linear-gradient(180deg, rgb(255 255 255 / 0%) 50%, #000000 100%);
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0px;
    width: 100%;
    height: 100%;
  }
  img{
    width: 100%;
  }
  .e-branding{
    position: absolute;
    left: 0;
    top: 0;
    width: initial;
  }
}



/* -- CTA -- */
.e-cta{
  background-color: $black;
  border-top: 1px solid $dark;
}
.e-cta{
  .e-cta-content{
    padding: 100px 0;
  }
  .e-cta-content:first-child{
    border-right: 1px solid $dark;
  }
  .e-cta-search::before{
    content: " ";
    width: 22px;
    height: 22px;
    margin-right: 15px;
    position: relative;
    top: 2px;
    display: inline-block;
    background: url("../images/icons/search-white.svg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    cursor: pointer;
  }
  .e-cta-search{
    @extend %paragraph;
    text-transform: uppercase;
    font-size: 24px;
    line-height: 0;
    cursor: pointer;
  }
  .e-cta-link{
    @extend %heading;
    @extend %linkTransition;
    font-size: 36px;
    letter-spacing: -0.02em;
    &:hover {
      @extend %linkTransitionActive;
    }
  }
  .e-link-arrow{
    top: -5px;
  }
}


