/* -- Header -- */
header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9;
  padding: 30px;
}
.e-nav-item{
  @extend %paragraph;
  font-size: 14px;
  line-height: 25px;
  cursor: pointer;
  background-image: linear-gradient(180deg, transparent 85%, $primary-color 0);
  @extend %linkTransition;
  &:hover {
      @extend %linkTransitionActive;
    }
}
.e-nav-item.e-active{
  @extend %linkTransitionActive;
}


/* -- Menu -- */
.e-menu{
  background-color: $white;
  margin-top: 30px;
}
.e-left-menu{
  padding: 30px;
  height: 100%;
  background-color: $dark-blue;
  h6{
    font-size: 18px;
  }
}
.e-menu-link{
  @extend %paragraph;
  font-size: 16px;
  line-height: 20px;
  @extend %linkTransition;
  background-image: linear-gradient(180deg, transparent 92%, $primary-color 0);
}
.e-menu-link:hover, .e-menu-link.e-active{
  @extend %linkTransitionActive;
  color: $primary-color;
}
.e-right-menu{
  padding: 30px;
  h6{
    font-size: 18px;
    color: $black;
  }
  p, a{
    color: $black;
  }
  p{
    font-size: 16px;
  }
  ul{
    padding-left: 15px;
  }
  ul li a, ul li{
    color: $primary-color;
    font-size: 16px;
    line-height: 20px;
    font-weight: 300;
  }
  ul li a{
    text-decoration: underline;
    margin-bottom: 15px;
  }
  .e-close{
    background: url("../images/icons/close.svg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    cursor: pointer;
    width: 18px;
    height: 18px;
    display: inline-block;
    position: absolute;
    right: 40px;
  }
}


/* -- Footer -- */
footer{
  background: $dark-blue;
  padding: 100px 0;
}
footer{
  p{
    font-size: 14px;
    color: $light-grey;
    font-weight: 300;
  }
  hr{
    background: $white;
    opacity: .2;
  }
  .e-link{
    font-size: 14px;
    line-height: 18px;
    color: #7493af;
    font-weight: 300;
    margin-bottom: 28px;
  }
  .e-hover:hover{
    color: #fff;
  }
  .e-rights{
    opacity: 0.5;
    font-size: 12px;
    line-height: 18px;
  }
  .e-address-wrap a{
    @extend %paragraph;
    font-size: 14px;
  }
}


/* -- Social widget -- */
.e-social-icon{
  margin-right: 58px;
}
.e-social-widget{
  .e-social-icon{
    margin-right: 25px;
  }
}

